.piggy-container-new.piggy-hover:hover, .piggy-container-new.piggy-hovered {
  background-color: #6480EB; }
  .piggy-container-new.piggy-hover:hover .piggy-right-container .piggy-desc-container .main-text, .piggy-container-new.piggy-hovered .piggy-right-container .piggy-desc-container .main-text {
    color: white; }
  .piggy-container-new.piggy-hover:hover .piggy-right-container .piggy-desc-container .desc-text, .piggy-container-new.piggy-hovered .piggy-right-container .piggy-desc-container .desc-text {
    color: white; }
  .piggy-container-new.piggy-hover:hover .piggy-img-container, .piggy-container-new.piggy-hovered .piggy-img-container {
    filter: brightness(1000) saturate(0); }

.piggy-container-new {
  border: solid 1px #6480EB;
  margin: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 15px !important;
  transition: background-color 0.3s ease; }
  .piggy-container-new.piggy-hover {
    cursor: pointer; }
  .piggy-container-new .wrap-container {
    flex-wrap: wrap;
    padding: 0;
    align-items: center; }
  .piggy-container-new .piggy-right-container {
    padding-left: 5px;
    flex: 1; }
    .piggy-container-new .piggy-right-container .piggy-desc-container {
      min-height: 48px;
      display: flex;
      flex-direction: column;
      justify-content: center; }
      .piggy-container-new .piggy-right-container .piggy-desc-container .main-text {
        margin-top: 10px;
        font-size: 24px;
        font-weight: 800;
        color: #6480EB;
        line-height: 15px;
        transition: color 0.3s ease; }
        @media screen and (max-width: 468px) {
          .piggy-container-new .piggy-right-container .piggy-desc-container .main-text {
            font-size: 16px; } }
        .piggy-container-new .piggy-right-container .piggy-desc-container .main-text img {
          margin: 0 5px;
          max-width: 26px;
          max-height: 26px; }
      .piggy-container-new .piggy-right-container .piggy-desc-container .desc-text {
        font-size: 10px;
        font-weight: 600;
        color: #6480EB;
        line-height: normal;
        transition: color 0.3s ease; }
        @media screen and (max-width: 468px) {
          .piggy-container-new .piggy-right-container .piggy-desc-container .desc-text {
            font-size: 10px; } }
      .piggy-container-new .piggy-right-container .piggy-desc-container .desc-padding {
        margin-top: -5px;
        margin-bottom: 0; }
      @media screen and (max-width: 568px) {
        .piggy-container-new .piggy-right-container .piggy-desc-container .main-text {
          line-height: 18px; } }
